.page {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.page__body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav__item {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  margin-right: 20px;
  padding: 0;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:last-child {
  margin-right: 0;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  place-content: center;
  gap: 46px 48px;
  margin-top: 50px;
  margin-bottom: 72px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  display: block;
}

.card__product {
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 32px 16px 16px;
  display: flex;
}

.card__content {
  margin-top: 40px;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__name {
  color: #060b35;
  margin: 0 0 4px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  margin: 0;
  transition: color .3s;
}

.card__price-title:hover {
  color: #34568b;
}

.card__price-current {
  text-transform: uppercase;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  transition: background-color .3s, color .3s;
}

.card__button-text {
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-radius: 5px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: block;
}

.stars__star:nth-child(5) {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
